import React from "react";

const TodosSide = () => (

    <div className="card">
        <div className="card-header">
            Today's Date
        </div>
        <div className="card-body">
            <p className="card-text"><h1>With supporting text below as a natural lead-in to additional content.</h1></p>
        </div>
    </div>

);

export default TodosSide;


